body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.grid-content{
  display: grid;
  grid-template-columns: repeat(3, minmax(50px, 1fr));
  row-gap: 10px;
  column-gap: 10px;
}

.main-content{
  background-color: rgb(255, 226, 183);
  max-width: 1100px;
  margin: 30px auto;
  padding: 30px;
}

.main-content h1{
  text-align: center;
}

.content-one h2{
  text-align: center;
}

.content-one{
  background-color: rgb(255, 208, 137);
  max-width: 650px;
  margin:10px auto;
  padding:30px;
  box-shadow: 2px 5px 5px 5px rgb(145, 89, 0);
  /* color:white; */
}

button {
  display: inline-block;
  margin: 5px 0;
  padding: 5px 10px;
  border-radius: 4px;
  border: none;
  background-color: #d39c5a;
  cursor: pointer;
  font-weight: bold;
  font-size: 20px;
}

button:hover {
  background-color: #542e00;
  color: white;
}

.count-content{
  max-width:200px;
  margin:20px auto;
  /* background-color: aliceblue; */
}

.count-content button{
  width:20%;
  padding:10px 5px;
  margin:0px 10px;
}

.summary h1{
  font-size: 25px;
}

.summary{
  max-width: 300px;
  margin:20px auto;
  background-color: rgb(255, 232, 196);
  box-shadow: 2px 5px 5px 5px rgb(145, 89, 0);
  padding:10px;
}

.summary h2{
  font-size: 20px;
  max-width: 200px;
  margin:20px auto;
  /* margin-left:80px; */
}